import React, {useRef} from 'react';

const Footer = () => {

    const noticeList = useRef(null);

    var bln =true;
    var originTop = 0;

    const onleftclick = () =>{
        if(bln===true){
            bln=false;
            noticeList.current.prepend(noticeList.current.lastChild);
            noticeList.current.style.transition="all 0s ease 0s";
            noticeList.current.style.top=(originTop-50)+"px";
            originTop-=50;
            setTimeout(function(){
                noticeList.current.style.transition="all 0.35s ease 0s";
                noticeList.current.style.top=(originTop+50)+"px";
                originTop+=50;
            },10);
            setTimeout(function(){
                bln=true;
            },500);
        }
    }

    const onrightlick = () =>{
        if(bln===true){
            bln=false;
            noticeList.current.append(noticeList.current.firstChild);
            noticeList.current.style.transition="all 0s ease 0s";
            noticeList.current.style.top=(originTop+50)+"px";
            originTop+=50;
            setTimeout(function(){
                noticeList.current.style.transition="all 0.35s ease 0s";
                noticeList.current.style.top=(originTop-50)+"px";
                originTop-=50;
            },10);
            setTimeout(function(){
                bln=true;
            },500);
        }
    }

    
    var slideAni = ()=> { setInterval((function(){
        onrightlick();
    }),3000);}

    slideAni();

    return (
        <div className="footer">
            <div className="footer__notice">
                <div className="footer__notice__wrapper">
                    <div className="footer__notice__frontWrapper">
                        <span className="footer__notice__title"></span>
                        <ul ref={noticeList}>
                            <li> &#62; CLASSIC   </li> 
                            <li>Wavve CLASSIC   </li> 
                        </ul>
                    </div>
                    <div className="footer__notice__backWrapper">
                        <button className="footer__notice__prevBtn" onClick={onleftclick}>
                            <img src={require("../asset/icon/arrow-notice-left.png")} alt="notice_prevBtn"></img>
                        </button>
                        <button className="footer__notice__nextBtn" onClick={onrightlick}>
                            <img src={require("../asset/icon/arrow-notice-right.png")} alt="notice_prevBtn"></img>
                        </button>
                    </div>
                </div>
            </div>
            <div className="footer__menu">
                <ul>
                    <li></li>
                    <li></li>
                    <li> </li>
                    <li></li>
                    <li className="privacy"> </li>
                    <li></li>
                </ul>
                <p>
                    <span className="first"> </span>
                    <span> </span>
                    <span className="last"> 1599-3709 ( 09:00~18:00 /  12:00~13:00 /    )</span>
                    <br></br>
                    <span className="first"> 220-88-38020</span>
                    <span>  :  2015--0128</span>
                    <span className="last">   : http://www.ftc.go.kr/bizCommPop.do?wrkr_no=220-88-38020</span>
                    <br></br>
                    <span className="first">    82, SBS   14</span>
                    <span className="last"> : helpdesk@wavve.com</span>
                    <br></br>
                </p>
                <span className="footer__menu__copyright">
                Copyright&copy; &#40;&#41; All rights reserved.
                </span>
                <div className="footer__menu__etc">
                    <div className="logo"><img src={require("../asset/wavvon logo.png")} alt="wavvonlogo"></img></div>
                    <select>
                        <option>  </option>
                        <option>  </option>
                        <option> PC </option>
                        <option>  </option>
                        <option>  </option>
                        <option>  </option>
                        <option>  </option>
                    </select>
                    <button className="fb"><img src={require("../asset/icon/fb.png")} alt="facebook"></img></button>
                    <button className="post"><img src={require("../asset/icon/post.png")} alt="naver post"></img></button>
                </div>
            </div>
        </div>
    );
};

export default Footer;